.wallet-balance {
  background-color: rgba(167, 133, 111, .9); /* This matches the table background color */
  border-radius: 8px;
  padding: 20px;
  margin-bottom: 20px;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.651);
  width: 100%;
  max-width: 400px;
  box-sizing: border-box;
}

.wallet-balance h2 {
  margin-bottom: 15px;
  color: #ffffff;
  font-size: 24px;
}

.account-info {
  background-color: rgba(118, 95, 79, 1); /* Keep it dark */
  border-radius: 8px;
  padding: 10px;
  margin-bottom: 15px;
  width: 100%; /* Ensure it takes full width of its container */
  box-sizing: border-box; /* Include padding in width calculation */
}

/* If the text is hard to read on this new background, you might want to adjust the text color */
.account-info, .account-info * {
  color: #ffffff; /* Ensure text is white for better contrast */
}

.connection-status {
  font-weight: bold;
  color: #4CAF50;
  margin-bottom: 15px;
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1); /* Optional: adds a subtle separator line */

}

.account-details {
  display: flex;
  flex-direction: column; /* Stack items vertically */
  align-items: flex-start;
}

.account-label {
  font-weight: bold;
  color: #ffffff;
  margin-bottom: 5px;
}

.address-value-container {
  width: 100%; /* Take full width */
  cursor: pointer;
  display: inline-block;
  background-color: rgba(45, 45, 45, 0.8);
  padding: 5px 10px;
  border-radius: 3px;
  border: 1px solid #333;
  text-align: left;
  box-sizing: border-box;
  min-width: 120px; /* Adjust this value based on your needs */
  transition: background-color 0.3s ease;
}

.address-value-container:hover {
  background-color: rgba(60, 60, 60, 0.8);
}

.address-value {
  font-family: monospace;
  color: #ffffff;
  word-break: break-all; /* Ensure long addresses wrap */
}

.copy-message {
  color: #4CAF50;
  font-weight: 400;
}

.balance-container {
  display: flex;
  justify-content: space-around;
}

.balance-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.balance-circle {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: rgba(45, 45, 45, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}

.balance-amount {
  font-size: 24px;
  font-weight: bold;
  color: #4CAF50;
  display: inline-block;
  width: 60px;
  text-align: center;
}

.balance-label {
  font-size: 14px;
  color: #ffffff;
  text-transform: uppercase;
}

.address-value.clickable {
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.address-value.clickable:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

/* Mobile Styles */
@media screen and (max-width: 768px) {
  .wallet-balance {
    padding: 15px;
    max-width: 100%;
  }

  .account-info {
    padding: 8px;
  }

  .account-details {
    flex-direction: column;
    align-items: flex-start;
  }

  .address-value-container {
    margin-top: 5px;
    width: 100%;
  }

  .balance-container {
    flex-direction: column;
    gap: 15px;
  }

  .balance-circle {
    width: 100px;
    height: 100px;
  }

  .balance-amount {
    font-size: 20px;
  }
}
